import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { ServiceCategoryComponent } from './service-category.component';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { DetailCategoryServicesComponent } from './detail-category-services/detail-category-services.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { TranslateModule } from '@ngx-translate/core';
import { ScrollingModule } from '@angular/cdk/scrolling';
import { DetailServiceComponent } from './detail-service/detail-service.component';
import { IonicModule } from '@ionic/angular';
const routes: Routes = [
  {
    path: '',
    component: ServiceCategoryComponent
  },
  {
    path: 'detail',
    component: DetailCategoryServicesComponent
  },
  {
    path: 'edit',
    component: DetailServiceComponent
  }
]

@NgModule({
  declarations: [
    ServiceCategoryComponent,
    DetailCategoryServicesComponent,
    DetailServiceComponent
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    FormsModule,
    ReactiveFormsModule,
    TranslateModule,
    ScrollingModule,
    IonicModule
  ]
})
export class ServiceCategoryModule { }
