import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { VhAlgorithm, VhImage, VhQuerySales } from 'ionic-vhframeworks';
import { LanguageService } from 'src/app/sales/services/language.service';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { VhType } from 'src/app/sales/interface/vh-type';

@Component({
  selector: 'app-detail-service',
  templateUrl: './detail-service.component.html',
  styleUrls: ['./detail-service.component.scss']
})
export class DetailServiceComponent implements OnInit {
  data: any = [];
  checked: boolean = true;
  showPopup: boolean = false;
  ProductServiceForm: FormGroup;
  barcode: boolean = true;
  list_category: any = [];
  list_barcode: any;
  price: any;
  category: any
  constructor(
    private router: Router,
    private vhImage: VhImage,
    public vhAlgorithm: VhAlgorithm,
    private vhComponent: VhComponent,
    private languageService: LanguageService,
    private vhQuerySales: VhQuerySales
  ) {
    this.data = this.router.getCurrentNavigation().extras.state.product;
    this.category = this.router.getCurrentNavigation().extras.state.category;
    this.ProductServiceForm = new FormGroup({
      img: new FormControl(this.data.img),
      name: new FormControl(this.data.name, Validators.required),
      barcode: new FormControl(this.data.barcode),
      unit: new FormControl(this.data.unit),
      price: new FormControl(this.data.price, Validators.compose([Validators.required, Validators.pattern("(^[0?=[.]] |^[^.,-][,0-9]*)(([.](?=[0-9]){1}[0-9]{0,5})|)")])),
      id_category: new FormControl(this.data.id_category, Validators.required),
      // allow_sell: new FormControl(this.data.allow_sell),
      selling: new FormControl(this.data.selling),
      type: new FormControl(this.data.type)
    });
  }
  ngOnInit(): void {
    this.initData();
  }

  ngAfterViewInit() {
    this.vhAlgorithm.waitingStack().then(() => {
      this.price = this.vhAlgorithm.vhnumeral(".price");
    });
  }

  initData() {
    // lấy danh mục
    this.list_category = this.vhQuerySales.getlocalCategorys().filter(res => res.type == VhType.SERVICE);

    //lay tat ca barcode san pham
    this.list_barcode = [];
    for (let item of this.vhQuerySales.getlocalProducts().filter(res => res.type == VhType.SERVICE)) {
      if (item.barcode) {
        this.list_barcode.push(item.barcode);
      }
    }
  }


  setBarcode(){
    let newbarcode = '';
    for (let index = 0; index < 12; index++) {
      newbarcode += Math.floor(Math.random() * 10)
    }
    this.checkBarcode(newbarcode);
    if(this.barcode){
      this.ProductServiceForm.controls['barcode'].setValue(newbarcode)
      this.updateServiceForm('barcode', {barcode: this.ProductServiceForm.value['barcode']})
    }
  }

  checkBarcode(barcode: string) {
    let detailproduct = this.vhQuerySales.getlocalDetailProduct_byBarcode(barcode)
    if(detailproduct == null){
      this.barcode = true;
    }else{
      this.barcode = false
    }
  }

  getFile() {
    document.getElementById("file-upload").click();
  }

  /** Lấy hình ảnh từ Desktop */
  onUpload(e?) {
    const file = e.target.files[0];
    this.vhImage.getThumbnailFromDesktop(file, 256, 256, "products", this.ProductServiceForm.value["img"] || '').then(
      photoURL => {
        this.ProductServiceForm.controls["img"].setValue(photoURL);
        this.vhComponent.showToast(2000, (this.languageService.translate("Image was loaded successfully")), "success-toast");
        this.updateServiceForm('img',{ img: this.ProductServiceForm.value.img});
      },
      () => {
        this.vhComponent.showToast(2000, (this.languageService.translate("Image was failed")), 'alert-toast');
      }
    );
  }

  /**Hàm cập nhật sản phẩm */
  updateServiceForm(field: string, object) {
    if(field == 'price'){
      if (this.ProductServiceForm.valid && !this.barcode) {
        if (this.price) {
          object.price = parseFloat(this.price.getRawValue());
        }
      }
    }
    this.vhQuerySales.updateProduct(this.data._id, object)
  }
  /** ------ Modal Zorro---- */
  isVisible = false;

  showModal(): void {
    this.isVisible = true;
  }

  handleOk(): void {
    this.deleteService();
  }

  handleCancel(): void {
    this.isVisible = false;
  }

  deleteService() {
    this.vhQuerySales.deleteProduct(this.data._id)
      .then(bool => {
        if (bool) {
          if (this.ProductServiceForm.value["img"]) {
            this.vhImage.deleteThumbnail([this.ProductServiceForm.value["img"]])
            this.vhComponent.showToast(2000, `${this.languageService.translate("Service")} ${this.languageService.translate("has been deleted successfully")}`, "success-toast");
              setTimeout(() => {
                this.isVisible = false;
                this.goBack();
              }, 300);
          } else {
            this.vhComponent.showToast(2000, `${this.languageService.translate("Service")} ${this.languageService.translate("has been deleted successfully")}`, "success-toast");
            setTimeout(() => {
              this.isVisible = false;
              this.goBack();
            }, 300);
          }
        } else {
          this.vhComponent.showToast(2000, (this.languageService.translate("Can't delete this product")), "alert-toast");
        }
      });
  }


  /** ------------------------- */

  goBack() {
    this.router.navigate(['/sales/dashboard/manage/products/service-category/detail'], { state: { category: this.category } });
  }

}
